 0;\r\n}\r\n\r\n.widget-message{\r\n    text-align: center;\r\n    padding: $l-size;\r\n    font-size: $n-size;\r\n    color: $off-white;\r\n    margin: 0;\r\n    border-bottom: 1px solid lighten($light-blue, 10%);\r\n}",".option{\r\n    display: flex;\r\n    justify-content: space-between;\r\n    padding: $l-size $n-size;\r\n    border-bottom: 1px solid lighten($light-blue, 10%);\r\n}\r\n\r\n.option__text{\r\n    color: white;\r\n    font-weight: 500;\r\n    font-size: 2rem;\r\n    margin: 0;\r\n    word-break: break-all;\r\n}\r\n\r\n",".add-option{\r\n    display: flex;\r\n    padding: $n-size;\r\n}\r\n\r\n.add-option__input{\r\n    background-color: $dark-blue;\r\n    border: none;\r\n    border-bottom: .3rem solid darken($dark-blue, 10%);\r\n    flex-grow: 1;\r\n    padding: $n-size;\r\n    margin-right: $s-size;\r\n    color: $off-white;\r\n    padding: $s-size;\r\n    margin: 0 0 $s-size 0;\r\n}\r\n\r\n.add-option-error{\r\n    color: $off-white;\r\n    font-style: italic;\r\n    padding: 0 $n-size;\r\n    margin: $n-size 0 0 0;\r\n}\r\n\r\n@media (min-width: $desktop-breakpoint){\r\n    .add-option {\r\n        flex-direction: row;\r\n    }\r\n    .add-option__input {\r\n        margin: 0 $s-size 0 0;\r\n    }\r\n}",".ReactModalPortal > div{\r\n    opacity: 0;\r\n}\r\n\r\n.ReactModalPortal .ReactModal__Overlay{\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    transition: opacity 200ms ease-in-out;\r\n}\r\n\r\n.ReactModalPortal .ReactModal__Overlay--after-open{\r\n    opacity: 1;\r\n}\r\n\r\n.ReactModalPortal .ReactModal__Overlay--before-close{\r\n    opacity: 0;\r\n}\r\n\r\n.modal{\r\n    background: $light-blue;\r\n    color: white;\r\n    max-width: 30rem;\r\n    outline: none;\r\n    padding: $l-size;\r\n    text-align: center;\r\n}\r\n\r\n.modal__title{\r\n    margin: 0 0 $n-size 0;\r\n}\r\n\r\n.modal__body{\r\n    font-size: 2rem;\r\n    font-weight: 300;\r\n    margin: 0 0 $l-size 0;\r\n    word-break: break-all;\r\n}",".container{\r\n    max-width: 60rem;\r\n    margin: 0 auto;\r\n    padding: 0 $n-size;\r\n}",".header{\r\n    background: $off-black;\r\n    margin-bottom: $xl-size;\r\n    padding: $n-size;\r\n    color: $off-white;\r\n}\r\n\r\n// BEM block element modifier\r\n.header__title{\r\n    font-size: $l-size;\r\n    margin: 0;\r\n}\r\n\r\n.header__subtitle{\r\n    color: rgb(138, 138, 163);\r\n    font-size: $n-size;\r\n    font-weight: 500;\r\n    margin: 0;\r\n}\r\n\r\n@media(min-width: $desktop-breakpoint){\r\n    .header{\r\n        margin-bottom: $xl-size;\r\n    }\r\n}"],"sourceRoot":""}]);
